<template>
	<div class="flex-between flex-vertical" style="width: 310px;">
		<my-hexagon style="width:90px;height: 100px;">{{label}}</my-hexagon>
		<!-- :bind 和 @input 等于 v-model -->
		<input :type="type"
			:bind="value" 
			@input="$emit('input',$event.target.value)"
			style="width: calc(100% - 90px);height:56px;padding:8px;background-color: rgba(255,255,255,.5);font-size: 22px;"
		/>
	</div>
</template>

<script>
	import myHexagon from '@/components/myHexagon.vue';
	export default {
		props:{
			label: {
				type: String
			},
			value: {
				type: String
			},
			type: {
				type: String,
				default: 'text'
			}
		},
		components:{
			'my-hexagon': myHexagon
		}
	}
</script>
