<template>
	<transition
		:css="false"
		appear
		@leave="_leave"
	>
		<div id="login" ref="login" class="flex-center position-center background">
			<div>
				<div class="flex-horizontal" style="margin-bottom: 8px;"><b style="font-size: 28px;">西龙池抽水蓄能</b></div>
				<div class="flex-horizontal" style="margin-bottom: 32px;"><b style="font-size: 40px;">台账管理平台</b></div>
				<div>
					<my-input label="账户" v-model="userName" style="margin-bottom: 8px;"/>
					<my-input label="密码" v-model="userPswd" style="margin-bottom: 32px;margin-left: 50px;" type="password"/>
					<my-button @click="_login">登录</my-button>
				</div>
			</div>
		</div>
	</transition>
</template>

<script>
	import myInput from '@/components/myInput.vue';
	import myButton from '@/components/myButton.vue';
	import animate from 'velocity-animate/velocity';
	export default {
		name: 'v-login',
		data() {
			return {
				userName: '',
				userPswd: ''
			}
		},
		components:{
			myInput,
			myButton
		},
		methods: {
			_leave(el,done){
				animate(el,{ opacity: 0 }, { duration: 600, easing: 'easeInCubic' });
				animate(el.children[0],{ scale: 5 }, { duration: 600 , easing: 'easeInCubic', complete: done });
			},
			_login(){
				this.$router.push('/index');
			}
		}
	}
</script>

<style scoped>
	.background {
		width: 100%;
		height: 100%;
		background-image: url('../../assets/img/back.png');
		background-position: center center;
		background-size: auto 100%;
		color: #fff;
	}
</style>
